import React, { Component } from "react";
import { getTeams } from "../services/standingsService";
import Table from "./common/table";
import "../css/standings.css";

class Standings extends Component {
	state = {
		teams: [],
	};

	// columns of standings table
	columns = [
		{ path: "rank", lable: "#" },

		{
			key: "standingsTeam",
			content: (team) => (
				<React.Fragment>
					<img
						src={team.team.logo}
						className="standings-team-logo mx-2"
						alt="logo"
					/>
					{team.team.name}
				</React.Fragment>
			),
			lable: "",
		},
		{ path: "all.played", lable: "" },
		{ path: "all.win", lable: "" },
		{ path: "all.draw", lable: "" },
		{ path: "all.lose", lable: "" },
		{ path: "all.goals.for", lable: " " },
		{ path: "all.goals.against", lable: " " },
		{ path: "goalsDiff", lable: "" },
		{ path: "points", lable: "" },
	];

	async componentDidMount() {
		const teams = await getTeams();
		this.setState({ teams });
	}

	render() {
		return (
			<div dir="rtl" className="row  global-card m-0 px-3 py-3">
				<div className="col-12 table-responsive">
					<Table columns={this.columns} data={this.state.teams} />
				</div>
			</div>
		);
	}
}

export default Standings;
